/* GLOBAL */
/* Background Colors */
/* здесь используйте глобальные селекторы img, body, html и так далее*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  height: 100%;
}
body {
  margin: 0;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat.woff2") format("woff2"), url("../fonts/montserrat.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: 400;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: 400;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
  font-display: swap;
  font-weight: 600;
  font-style: 400;
}
/* BLOCKS */
.logo {
  display: flex;
  margin-right: auto;
  gap: 20px;
  align-items: center;
}
.logo__text {
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #333333;
  display: none;
}
@media (min-width: 1360px) {
  .logo__text {
    display: inline-flex;
  }
}
@media (min-width: 728px) {
  .logo__image--small {
    display: none;
  }
}
.logo__image--middle {
  display: none;
}
@media (min-width: 728px) {
  .logo__image--middle {
    display: block;
  }
}
@media (min-width: 1360px) {
  .logo__image--middle {
    display: none;
  }
}
.logo__image--big {
  display: none;
}
@media (min-width: 1360px) {
  .logo__image--big {
    display: block;
  }
}
.button {
  cursor: pointer;
  padding: 0;
  margin: 0;
  min-width: 141px;
  min-height: 36px;
  border: none;
  background-color: #7859cf;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #333333;
  color: #ffffff;
  border-radius: 4px;
}
.button-menu {
  width: 60px;
  height: 60px;
  align-self: center;
  border: none;
  background: #ffffff;
  position: relative;
  cursor: pointer;
  margin-left: 22px;
}
.button-menu:hover {
  background: #f0ebff;
}
@media (min-width: 728px) {
  .button-menu {
    display: none;
  }
}
.button-menu__icon--close {
  display: none;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  min-width: 320px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #333333;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 728px) {
  .container {
    width: 728px;
