  line-height: 16px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #333333;
  display: inline-block;
  text-transform: uppercase;
  padding: 32px 20px;
  position: relative;
}
.main-nav__link--current::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 2px solid #7859cf;
}
.main-nav__link:hover,
.main-nav__link:focus-within {
  color: #7859cf;
  background-color: #F0EBFF;
}
.main-nav__link:active,
.main-nav__link:focus-within:active {
