  padding-left: 10px;
  min-width: max-content;
}
/* Контейнер */
.control {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
/* Скрыть переключатель браузера по умолчанию */
.control input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
/* Создание пользовательского переключателя */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 24px;
  width: 24px;
  border: 2px solid #bdbdbd;
  border-radius: 50%;
}
/* При наведении курсора мыши добавьте серый цвет фона */
.control:hover input ~ .checkmark,
.control:focus-within input ~ .checkmark {
  background-color: #ccc;
}
/* Когда переключатель установлен, добавьте синий фон */
.control input:checked ~ .checkmark {
  background-color: #ffffff;
  border-color: #7859cf;
}
/* Создайте индикатор (точка/круг-скрыт, если не установлен флажок) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
/* Показывать индикатор (точка / круг), когда он установлен */
.control input:checked ~ .checkmark:after {
  display: block;
}
/* Стиль индикатора (точка / круг) */
.control .checkmark:after {
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #7859cf;
}
.tumbler-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.tumbler-list__switch {
  position: relative;
  display: inline-flex;
  width: 42px;
  height: 24px;
}
.tumbler-list__switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.tumbler-list__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.tumbler-list__slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.tumbler-list__name {
  padding-left: 40px;
  min-width: max-content;
}
input:checked + .tumbler-list__slider {
  background-color: #7859cf;
}
input:focus + .tumbler-list__slider {
  box-shadow: 0 0 1px #7859cf;
}
input:checked + .tumbler-list__slider:before {
  -webkit-transform: translateX(18px);
  -ms-transform: translateX(18px);
  transform: translateX(18px);
}
/* Закругленные ползунки */
.tumbler-list__slider.round {
  border-radius: 12px;
}
.tumbler-list__slider.round:before {
  border-radius: 50%;
}
